// Colors
$color-light: #FFFBF2;
$color-dark: #363033;
$color-link: #0072B9;

// Spacing
$sp-vsmall: 6px;
$sp-small: 12px;
$sp-medium: 24px;
$sp-large: 36px;
$sp-xlarge: 48px;

// Typography
@font-face {
    font-family: "Geologica";
    src: url("../fonts/geologica.ttf") format("truetype");
    font-weight: 200 900;
    font-style: normal;
}

$ff: 'Geologica', sans-serif;

$fs-body: 18px;
$lh-body: (
    $fs-body * 1.5
);

$fw-extralight: 200;
$fw-light: 300;
$fw-normal: 400;
$fw-bold: 700;
$fw-black: 900;